\n\t\tborder-radius: 50%;\n\t\t// background-color: $color-white;\n\t\t// box-shadow: 0 1rem 3rem rgba($color-black, 0.2);\n\t\tcursor: pointer;\n\t\tz-index: 12;\n\n\t\t// Center the Icon\n    text-align: center;\n    @include media(\">=phone\", \"<=tablet\") {\n      top: 1rem;\n      right: 1rem;\n    }\n\t}\n\n\t// Background Image / Gradient -> Hidden Behind a Label\n\t&__background {\n\t\tposition: fixed;\n\t\t// top: 2rem;\n\t\t// right: 2rem;\n\t\twidth: 100vw;\n    height: 100vh;\n    display: none;\n    top: 0;\n    left: 50%;\n    transform: translateX(-50%);\n\t\t// border-radius: 50%;\n\t\tbackground-image: radial-gradient(\n\t\t\t$color-primary-light,\n\t\t\t$color-primary-dark\n    );\n    opacity: 0;\n\t\tz-index: 10;\n\n    // transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);\n    transition: opacity 0.8s ease-in,\n\t\t\ttransform 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);\n\t}\n\n\t// Menu Container\n\t&__nav {\n\t\tposition: fixed;\n\t\ttop: 0;\n\t\tleft: 50%;\n\t\theight: 100vh;\n\t\twidth: 100%;\n\t\tz-index: 11;\n\n\t\ttransform: translateX(-50%);\n\t\tvisibility: hidden;\n\t\topacity: 0;\n\n\t\ttransition: opacity 0.8s ease-in,\n\t\t\ttransform 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);\n\t}\n\n\t// Unordered List\n\t&__list {\n\t\t@include absolute-center;\n\t\ttext-align: center;\n    list-style: none;\n    @include media(\">=phone\", \"<=tablet\") {\n      width: 100%;\n    }\n\t}\n\n\t// List Items with Automatic Counter\n\t&__item {\n\t\tmargin: 1rem;\n\t\tcounter-increment: list;\n\t}\n\n\t// Item Links\n\t&__link {\n\t\t// Automatic Counter\n\t\t&::before {\n\t\t\tcontent: '0' counters(list, '');\n      padding-right: 1.2rem;\n      @include media(\">=phone\", \"<=tablet\") {\n        padding-right: .6rem;\n      }\n\t\t}\n\n\t\t&:link,\n\t\t&:visited {\n\t\t\tdisplay: inline-block;\n\t\t\tfont-size: 1.6rem;\n\t\t\tfont-weight: 700;\n\t\t\tpadding: 1rem 2rem;\n\t\t\tcolor: $color-black;\n\t\t\ttext-decoration: none;\n\t\t\ttext-transform: uppercase;\n\t\t\tbackground-image: linear-gradient(\n\t\t\t\t120deg,\n\t\t\t\ttransparent 0%,\n\t\t\t\ttransparent 49.9%,\n\t\t\t\t$color-white 50%\n\t\t\t);\n\t\t\tbackground-size: 227%;\n\n      transition: all 0.4s;\n      @include media(\">=phone\", \"<=tablet\") {\n        font-size: 1rem;\n        padding: .5rem 1rem;\n      }\n\t\t}\n\n\t\t&:hover,\n\t\t&:active {\n\t\t\tcolor: $color-primary;\n\t\t\tbackground-position: 100%;\n\t\t\ttransform: translateX(1rem);\n\t\t}\n\t}\n\n// Icon Design\n\t&__icon {\n\t\tdisplay: inline-block;\n\t\tposition: relative;\n\t\twidth: 2rem;\n\t\theight: 0.5rem;\n\t\ttop: 37%;\n\n\t\ttransition: transform 0.25s ease-in-out;\n\n\t\t&-span {\n\t\t\tposition: absolute;\n\t\t\theight: 0.2rem;\n\t\t\twidth: 50%;\n\t\t\tbackground: $color-grey-dark-2;\n\n\t\t\ttransition: all 0.5s ease-in;\n\n\t\t\t&:nth-child(even) {\n\t\t\t\tleft: 50%;\n\t\t\t\tborder-radius: 0 0.9rem 0.9rem 0;\n\t\t\t}\n\t\t\t&:nth-child(odd) {\n\t\t\t\tleft: 0;\n\t\t\t\tborder-radius: 0.9rem 0 0 0.9rem;\n\t\t\t}\n\n\t\t\t&:nth-child(1),\n\t\t\t&:nth-child(2) {\n\t\t\t\ttransform: translateY(-.6rem);\n\t\t\t}\n\t\t\t&:nth-child(5),\n\t\t\t&:nth-child(6) {\n\t\t\t\ttransform: translateY(.6rem);\n\t\t\t}\n\t\t}\n\t}\n\n\t&__checkbox:checked + &__button &__icon-span {\n\t\t&:nth-child(1),\n\t\t&:nth-child(6) {\n\t\t\ttransform: rotate(45deg);\n\t\t}\n\t\t&:nth-child(2),\n\t\t&:nth-child(5) {\n\t\t\ttransform: rotate(-45deg);\n\t\t}\n\t}\n\n\t&__checkbox:checked + &__button &__icon-span {\n\t\t&:nth-child(2),\n\t\t&:nth-child(6) {\n\t\t\ttransform-origin: left;\n\t\t}\n\t\t&:nth-child(1),\n\t\t&:nth-child(5) {\n\t\t\ttransform-origin: right;\n\t\t}\n\t}\n\n\t&__checkbox:checked + &__button &__icon-span {\n\t\t&:nth-child(3) {\n\t\t\tleft: -50%;\n\t\t\topacity: 0;\n\t\t}\n\t\t&:nth-child(4) {\n\t\t\tleft: 100%;\n\t\t\topacity: 0;\n\t\t}\n\t}\n\n\t&__button:hover &__icon-span {\n\t\t&:nth-child(3) {\n\t\t\ttransform: translateX(-0.2rem) scale(1.1);\n\t