iDDxeA,mDAwBI,eAAgB,CAEnB,CAML,mCAGI,iBAAkB,CAClB,iBAAkB,CAClB,UAAW,CACX,WAAY,CACZ,gBAAiB,CACjB,iBAAkB,CARtB,iDAYI,qBAAsB,CACtB,mBAAoB,CACpB,UAAW,CACX,cAAe,CAfnB,kFAoBI,YAAa,CApBjB,iCAwBI,iBAAkB,CAClB,iBAAkB,CAClB,kBAAmB,CA1BvB,qCA8BI,gBAAiB,CACjB,iBAAkB,CAClB,gBAAiB,CAhCrB,qCAoCI,iBAAkB,CAClB,KAAM,CACN,MAAO,CACP,aAAc,CACd,iBAAkB,CAxCtB,yCA6CI,qBAAsB,CACtB,mBAAoB,CACpB,SAAU,CACV,qBAAsB,CACtB,eAAgB,CAChB,sBAAuB,CACvB,cAAe,CACf,iBAAkB,CAClB,UAAW,CAKZ,iDA1DH,yCAwDM,SAAU,CAEb,CA1DH,qCA6DI,iBAAkB,CAClB,KAAM,CACN,MAAO,CACP,aAAc,CACd,iBAAkB,CAjEtB,2CAqEI,kBAAmB,CACnB,aAAc,CACd,UAAW,CAMZ,iDA7EH,2CA0EM,YAAa,CACb,oBAAqB,CAExB,CA7EH,gFAiFI,gBAAiB,CAjFrB,uCAqFI,gBAAiB,CAClB,qBAKD,wBAAyB","file":"Chart.scss","sourcesContent":["@import '../styles/include-media';\n\n.dotchart,\n.linechart {\n  position: sticky;\n  top: 0;\n\n  &__container {\n    // position: absolute;\n    // top: 50%;\n    // left: 50%;\n    // margin-right: -50%;\n    // transform: translate(-50%, -50%);\n    // width: 100%;\n  }\n\n  &__header {\n    display: flex;\n    justify-content: space-between;\n    position: relative;\n\n    &__buttons {\n      display: flex;\n      justify-content: space-between;\n      margin-bottom: 20px;\n\n      button {\n        font-size: 16px;\n        font-weight: bold;\n        padding: 5px 10px;\n        border-radius: 20px;\n        border-style: none;\n        margin-right: 10px;\n        color: #9A9A9A;\n        cursor: pointer;\n\n        @include media(\">=phone\", \"<=tablet\") {\n          font-size: 16px;\n          line-height: 28px;\n          margin-bottom: 10px;\n        }\n      }\n\n      button.active {\n        color: #FFF;\n        background-color: #1e663b;\n      }\n    }\n\n    &__title {\n      color: #222;\n      // display:flex;\n      // align-items: center;\n      position: relative;\n\n      &:before {\n        content: '';\n        position: absolute;\n        top: 0px;\n        left: -70px;\n        width: 40px;\n        height: 40px;\n        border-radius: 50%;\n        background-color: black;\n      }\n\n      @include media(\">=phone\", \"<=tablet\") {\n        font-size: 1.2em;\n      }\n    }\n\n    &__title {\n      color: #222;\n      // display:flex;\n      // align-items: center;\n      position: relative;\n\n      &:before {\n        content: '';\n        position: absolute;\n        top: 0px;\n        left: -70px;\n        width: 40px;\n        height: 40px;\n        border-radius: 50%;\n        background-color: black;\n\n        @include media(\">=phone\", \"<=tablet\") {\n          left: -30px;\n          width: 20px;\n          height: 20px;\n        }\n      }\n\n      @include media(\">=phone\", \"<=tablet\") {\n        font-size: 1.2em;\n      }\n    }\n\n\n  }\n}\n\n.line001,\n.dot001 {\n  .wrapper {\n    position: relative;\n    align-self: center;\n    width: 100%;\n    height: 70vh;\n    margin-left: auto;\n    margin-right: auto;\n  }\n\n  .legend-wrapper {\n    box-sizing: border-box;\n    padding-bottom: 20px;\n    width: 100%;\n    flex-flow: wrap;\n  }\n\n  .legend-wrapper,\n  .legend {\n    display: flex;\n  }\n\n  .legend {\n    margin-right: 15px;\n    margin-bottom: 8px;\n    align-items: center;\n  }\n\n  .legend p {\n    margin: 0 0 0 8px;\n    width: max-content;\n    font-weight: bold;\n  }\n\n  .tool-tip {\n    position: absolute;\n    top: 0;\n    left: 0;\n    color: #DDD5AC;\n    padding: 0.3% 0.6%;\n  }\n\n\n  .text-block {\n    box-sizing: border-box;\n    margin-bottom: 35rem;\n    width: 15%;\n    padding: 1.2rem 0.5rem;\n    line-height: 1.6;\n    border: 2px solid black;\n    font-size: 16px;\n    position: relative;\n    z-index: 10;\n\n    @include media(\">=phone\", \"<=tablet\") {\n      width: 80%;\n    }\n  }\n\n  .tool-tip {\n    position: absolute;\n    top: 0;\n    left: 0;\n    color: #DDD5AC;\n    padding: 0.3% 0.6%;\n  }\n\n  .description {\n    margin-left: 0.8rem;\n    overflow: auto;\n    z-index: 10;\n\n    @include media(\">=phone\", \"<=tablet\") {\n      display: grid;\n      justify-items: center;\n    }\n  }\n\n  .axis text,\n  .label text {\n    font-weight: bold;\n  }\n\n  .axis path {\n    stroke-width: 2px;\n  }\n}\n\n\n.line001 .text-block {\n  background-color: #E0E0E0;\n}\n","@charset \"UTF-8\";\n\n//     _            _           _                           _ _\n//    (_)          | |         | |                         | (_)\n//     _ _ __   ___| |_   _  __| | ___   _ __ ___   ___  __| |_  __ _\n//    | | '_ \\ / __| | | | |/ _` |/ _ \\ | '_ ` _ \\ / _ \\/ _` | |/ _` |\n//    | | | | | (__| | |_| | (_| |  __/ | | | | | |  __/ (_| | | (_| |\n//    |_|_| |_|\\___|_|\\__,_|\\__,_|\\___| |_| |_| |_|\\___|\\__,_|_|\\__,_|\n//\n//      Simple, elegant and maintainable media queries in Sass\n//                        v1.4.9\n//\n//                http://include-media.com\n//\n//         Authors: Eduardo Boucas (@eduardoboucas)\n//                  Hugo Giraudel (@hugogiraudel)\n//\n//      This project is licensed under the terms of the MIT license\n\n\n////\n/// include-media library public configuration\n/// @author Eduardo Boucas\n/// @access public\n////\n\n\n///\n/// Creates a list of global breakpoints\n///\n/// @example scss - Creates a single breakpoint with the label `phone`\n///  $breakpoints: ('phone': 320px);\n///\n$breakpoints: (\n  'phone': 320px,\n  'tablet': 768px,\n  'desktop': 1024px\n) !default;\n\n\n///\n/// Creates a list of static expressions or media types\n///\n/// @example scss - Creates a single media type (screen)\n///  $media-expressions: ('screen': 'screen');\n///\n/// @example scss - Creates a static expression with logical disjunction (OR operator)\n///  $media-expressions: (\n///    'retina2x': '(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)'\n///  );\n///\n$media-expressions: (\n  'screen': 'screen',\n  'print': 'print',\n  'handheld': 'handheld',\n  'landscape': '(orientation: landscape)',\n  'portrait': '(orientation: portrait)',\n  'retina2x': '(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx)',\n  'retina3x': '(-webkit-min-device-pixel-ratio: 3), (min-resolution: 350dpi), (min-resolution: 3dppx)'\n) !default;\n\n\n///\n/// Defines a number to be added or subtracted from each unit when declaring breakpoints with exclusive intervals\n///\n/// @example scss - Interval for pixels is defined as `1` by default\n///  @include media('>128px') {}\n///\n///  /* Generates: */\n///  @media (min-width: 129px) {}\n///\n/// @example scss - Interval for ems is defined as `0.01` by default\n///  @include media('>20em') {}\n///\n///  /* Generates: */\n///  @media (min-width: 20.01em) {}\n///\n/// @example scss - Interval for rems is defined as `0.1` by default, to be used with `font-size: 62.5%;`\n///  @include media('>2.0rem') {}\n///\n///  /* Generates: */\n///  @media (min-width: 2.1rem) {}\n///\n$unit-intervals: (\n  'px': 1,\n  'em': 0.01,\n  'rem': 0.1,\n  '': 0\n) !default;\n\n///\n/// Defines whether support for media queries is available, useful for creating separate stylesheets\n/// for browsers that don't support media queries.\n///\n/// @example scss - Disables support for media queries\n///  $im-media-support: false;\n///  @include media('>=tablet') {\n///    .foo {\n///      color: tomato;\n///    }\n///  }\n///\n///  /* Generates: */\n///  .foo {\n///    color: tomato;\n///  }\n///\n$im-media-support: true !default;\n\n///\n/// Selects which breakpoint to emulate when support for media queries is disabled. Media queries that start at or\n/// intercept the breakpoint will be displayed, any others will be ignored.\n///\n/// @example scss - This media query will show because it intercepts the static breakpoint\n///  $im-media-support: false;\n///  $im-no-media-breakpoint: 'desktop';\n///  @include media('>=tablet') {\n///    .foo {\n///      color: tomato;\n///    }\n///  }\n///\n///  /* Generates: */\n///  .foo {\n///    color: tomato;\n///  }\n///\n/// @example scss - This media query will NOT show because it does not intercept the desktop breakpoint\n///  $im-media-support: false;\n///  $im-no-media-breakpoint: 'tablet';\n///  @include media('>=desktop') {\n///    .foo {\n///      color: tomato;\n///    }\n///  }\n///\n///  /* No output */\n///\n$im-no-media-breakpoint: 'desktop' !default;\n\n///\n/// Selects which media expressions are allowed in an expression for it to be used when media queries\n/// are not supported.\n///\n/// @example scss - This media query will show because it intercepts the static breakpoint and contains only accepted media expressions\n///  $im-media-support: false;\n///  $im-no-media-breakpoint: 'desktop';\n///  $im-no-media-expressions: ('screen');\n///  @include media('>=tablet', 'screen') {\n///    .foo {\n///      color: tomato;\n///    }\n///  }\n///\n///   /* Generates: */\n///   .foo {\n///     color: tomato;\n///   }\n///\n/// @example scss - This media query will NOT show because it intercepts the static breakpoint but contains a media expression that is not accepted\n///  $im-media-support: false;\n///  $im-no-media-breakpoint: 'desktop';\n///  $im-no-media-expressions: ('screen');\n///  @include media('>=tablet', 'retina2x') {\n///    .foo {\n///      color: tomato;\n///    }\n///  }\n///\n///  /* No output */\n///\n$im-no-media-expressions: ('screen', 'portrait', 'landscape') !default;\n\n////\n/// Cross-engine logging engine\n/// @author Hugo Giraudel\n/// @access private\n////\n\n\n///\n/// Log a message either with `@error` if supported\n/// else with `@warn`, using `feature-exists('at-error')`\n/// to detect support.\n///\n/// @param {String} $message - Message to log\n///\n@function im-log($message) {\n  @if feature-exists('at-error') {\n    @error $message;\n  } @else {\n    @warn $message;\n    $_: noop();\n  }\n\n  @return $message;\n}\n\n\n///\n/// Wrapper mixin for the log function so it can be used with a more friendly\n/// API than `@if im-log('..') {}` or `$_: im-log('..')`. Basically, use the function\n/// within functions because it is not possible to include a mixin in a function\n/// and use the mixin everywhere else because it's much more elegant.\n///\n/// @param {String} $message - Message to log\n///\n@mixin log($message) {\n  @if im-log($message) {}\n}\n\n\n///\n/// Function with no `@return` called next to `@warn` in Sass 3.3\n/// to trigger a compiling error and stop the process.\n///\n@function noop() {}\n\n///\n/// Determines whether a list of conditions is intercepted by the static breakpoint.\n///\n/// @param {Arglist}   $conditions  - Media query conditions\n///\n/// @return {Boolean} - Returns true if the conditions are intercepted by the static breakpoint\n///\n@function im-intercepts-static-breakpoint($conditions...) {\n  $no-media-breakpoint-value: map-get($breakpoints, $im-no-media-breakpoint);\n\n  @if not $no-media-breakpoint-value {\n    @if im-log('`#{$im-no-media-breakpoint}` is not a valid breakpoint.') {}\n  }\n\n  @each $condition in $conditions {\n    @if not map-has-key($media-expressions, $condition) {\n      $operator: get-expression-operator($condition);\n      $prefix: get-expression-prefix($operator);\n      $value: get-expression-value($condition, $operator);\n\n      @if ($prefix == 'max' and $value <= $no-media-breakpoint-value) or\n          ($prefix == 'min' and $value > $no-media-breakpoint-value) {\n        @return false;\n      }\n    } @else if not index($im-no-media-expressions, $condition) {\n      @return false;\n    }\n  }\n\n  @return true;\n}\n\n////\n/// Parsing engine\n/// @author Hugo Giraudel\n/// @access private\n////\n\n\n///\n/// Get operator of an expression\n///\n/// @param {String} $expression - Expression to extract operator from\n///\n/// @return {String} - Any of `>=`, `>`, `<=`, `<`, `≥`, `≤`\n///\n@function get-expression-operator($expression) {\n  @each $operator in ('>=', '>', '<=', '<', '≥', '≤') {\n    @if str-index($expression, $operator) {\n      @return $operator;\n    }\n  }\n\n  // It is not possible to include a mixin inside a function, so we have to\n  // rely on the `im-log(..)` function rather than the `log(..)` mixin. Because\n  // functions cannot be called anywhere in Sass, we need to hack the call in\n  // a dummy variable, such as `$_`. If anybody ever raise a scoping issue with\n  // Sass 3.3, change this line in `@if im-log(..) {}` instead.\n  $_: im-log('No operator found in `#{$expression}`.');\n}\n\n\n///\n/// Get dimension of an expression, based on a found operator\n///\n/// @param {String} $expression - Expression to extract dimension from\n/// @param {String} $operator - Operator from `$expression`\n///\n/// @return {String} - `width` or `height` (or potentially anything else)\n///\n@function get-expression-dimension($expression, $operator) {\n  $operator-index: str-index($expression, $operator);\n  $parsed-dimension: str-slice($expression, 0, $operator-index - 1);\n  $dimension: 'width';\n\n  @if str-length($parsed-dimension) > 0 {\n    $dimension: $parsed-dimension;\n  }\n\n  @return $dimension;\n}\n\n\n///\n/// Get dimension prefix based on an operator\n///\n/// @param {String} $operator - Operator\n///\n/// @return {String} - `min` or `max`\n///\n@function get-expression-prefix($operator) {\n  @return if(index(('<', '<=', '≤'), $operator), 'max', 'min');\n}\n\n\n///\n/// Get value of an expression, based on a found operator\n///\n/// @param {String} $expression - Expression to extract value from\n/// @param {String} $operator - Operator from `$expression`\n///\n/// @return {Number} - A numeric value\n///\n@function get-expression-value($expression, $operator) {\n  $operator-index: str-index($expression, $operator);\n  $value: str-slice($expression, $operator-index + str-length($operator));\n\n  @if map-has-key($breakpoints, $value) {\n    $value: map-get($breakpoints, $value);\n  } @else {\n    $value: to-number($value);\n  }\n\n  $interval: map-get($unit-intervals, unit($value));\n\n  @if not $interval {\n    // It is not possible to include a mixin inside a function, so we have to\n    // rely on the `im-log(..)` function rather than the `log(..)` mixin. Because\n    // functions cannot be called anywhere in Sass, we need to hack the call in\n    // a dummy variable, such as `$_`. If anybody ever raise a scoping issue with\n    // Sass 3.3, change this line in `@if im-log(..) {}` instead.\n    $_: im-log('Unknown unit `#{unit($value)}`.');\n  }\n\n  @if $operator == '>' {\n    $value: $value + $interval;\n  } @else if $operator == '<' {\n    $value: $value - $interval;\n  }\n\n  @return $value;\n}\n\n\n///\n/// Parse an expression to return a valid media-query expression\n///\n/// @param {String} $expression - Expression to parse\n///\n/// @return {String} - Valid media query\n///\n@function parse-expression($expression) {\n  // If it is part of $media-expressions, it has no operator\n  // then there is no need to go any further, just return the value\n  @if map-has-key($media-expressions, $expression) {\n    @return map-get($media-expressions, $expression);\n  }\n\n  $operator: get-expression-operator($expression);\n  $dimension: get-expression-dimension($expression, $operator);\n  $prefix: get-expression-prefix($operator);\n  $value: get-expression-value($expression, $operator);\n\n  @return '(#{$prefix}-#{$dimension}: #{$value})';\n}\n\n///\n/// Slice `$list` between `$start` and `$end` indexes\n///\n/// @access private\n///\n/// @param {List} $list - List to slice\n/// @param {Number} $start [1] - Start index\n/// @param {Number} $end [length($list)] - End index\n///\n/// @return {List} Sliced list\n///\n@function slice($list, $start: 1, $end: length($list)) {\n  @if length($list) < 1 or $start > $end {\n    @return ();\n  }\n\n  $result: ();\n\n  @for $i from $start through $end {\n    $result: append($result, nth($list, $i));\n  }\n\n  @return $result;\n}\n\n////\n/// String to number converter\n/// @author Hugo Giraudel\n/// @access private\n////\n\n\n///\n/// Casts